.language-toggler { display: block; text-transform: uppercase; left: 0; top: 0; position: relative; width: 21px; height: 35px; font-size: 6px; background: #fff; cursor: pointer; border: 1px #E30613 solid; }

.language-toggler:hover { color: #fff; }

