.language-toggler::before, .language-toggler::after { position: absolute; width: 100%; height: 50%; top: 0; display: block; background: #E30613; color: #fff; transition: all 350ms ease; cursor: pointer; text-align: center; line-height: 1.6; padding-top: 4px; }

.user { background-color: #B3B3B3; color: #fff; padding: 9px; margin-left: 8px; text-transform: uppercase; cursor: pointer; font-weight: 600; font-size: 12px; letter-spacing: 0.5px; }

